@import '../assets/less/size.less';
@import '../assets/less/color.less';

.ant-layout-content {
  width: 100vw;
  height: @content-height;
  align-items: stretch;
  min-width: @app-min-width;
}

.layout {
  min-width: @app-min-width;
}

.ant-carousel {
  background: #e2e2e2;
  height: 100%;

  .slick-slider {
    height: 100%;
  }

  .slick-slide {
    text-align: center;
    background: #364d79;
    overflow: hidden;
  }

  .slick-slide h3 {
    color: #fff;
  }

  .slick-list {
    height: 100%;

    .slick-track {
      height: 100%;
    }
  }

  .slick-dots.slick-dots-bottom {
    bottom: -45px;

    :nth-child(1) {
      margin-right: 20px;
      margin-left: 20px;
      button {
        background-image: url("../assets/img/menu-24.png");
        background-repeat: no-repeat;
        height: 24px;
        width: 30px;
      }
    }

    :nth-child(1).slick-active {
      button {
        background-image: url("../assets/img/menu-32.png");
        background-repeat: no-repeat;
        height: 32px;
        width: 40px;
      }
    }

    :nth-child(2){
      margin-right: 20px;
      margin-left: 20px;
      button {
        background-image: url("../assets/img/thermometer-24.png");
        background-repeat: no-repeat;
        height: 24px;
        width: 30px;
      }
    }

    :nth-child(2).slick-active {
      button {
        background-image: url("../assets/img/thermometer-32.png");
        background-repeat: no-repeat;
        height: 32px;
        width: 40px;
      }
    }

  }
}